.section {
   &__pokedex {
      padding: 80px 0 32px;
   }
}
.pokedex {
   &__header {
      margin-bottom: 40px;
   }
   &__title {
      font-family: "Karla";
      font-weight: 400;
      font-size: 35px;
      text-align: center;
      letter-spacing: 3px;
      color: #000000;
      margin-bottom: 34px;
      @media (max-width: 480px) {
         font-size: 24px;
      }
   }
   &__form {
      margin-bottom: 36px;
   }
   &__form-input {
      padding: 17px 30px;
      width: 100%;
      background: #f2f2f2;
      box-shadow: 4px 4px 16px rgba(1, 28, 64, 0.2);
      border-radius: 40px;
      border: none;
      color: rgba(33, 33, 33, 0.8);
      &::placeholder {
         color: rgba(33, 33, 33, 0.8);
      }
      &::-moz-placeholder {
         color: rgba(33, 33, 33, 0.8);
      }
      @media (max-width: 480px) {
         padding: 8px 18px;
      }
   }
   .filter {
      padding: 3px 36px 3px 15px;
      background: #f2f2f2;
      box-shadow: 2px 2px 2px rgba(33, 33, 33, 0.1);
      border-radius: 8px;
      font-family: "Source Sans Pro";
      font-weight: 400;
      font-size: 12px;
      color: #000000;
      max-width: 77px;
      margin-bottom: 30px;
      display: none;
      @media (max-width: 768px) {
         display: block;
         &.active {
            max-width: none !important;
            background: none;
            box-shadow: none;
            width: 24px;
            height: 20px;
            position: fixed;
            right: 0;
            top: 30px;
            z-index: 1001;
            &::after,
            &::before,
            span {
               content: "";
               position: absolute;
               width: 24px;
               height: 4px;
               left: 0;
               right: 0;
               border-radius: 5px;
               background-color: #212121;
               transition: 0.3s ease 0s;
               transform-origin: right;
            }
            &::before {
               top: 0px;
            }
            &::after {
               bottom: 0px;
            }
            span {
               top: 50%;
               transform: translate(0, -50%);
            }
            &.active {
               &::before {
                  transform: rotate(-45deg);
                  transition: 0.3s ease 0s;
               }
               &::after {
                  transform: rotate(45deg);
                  transition: 0.3s ease 0s;
               }
               span {
                  transform: translate(0, -50%) scaleX(0);
                  transition: 0.3s ease 0s;
               }
            }
         }
      }
      @media (max-width: 768px) and (max-height: 600px){
         &.active{
            position: absolute !important;
         }
      }
   }
   &__accordion {
      display: flex;
      gap: 64px;
      @media (max-width: 768px) {
         right: 0;
         z-index: 1000;
         gap: 20px;
         background: linear-gradient(180deg, #f5db13 0%, #f2b807 100%);
         box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.45);
         border-radius: 0px 0px 16px 16px;
         padding: 34px 27px 23px;
         flex-direction: column;
         position: fixed;
         left: 0;
         top: -100vh;
         transition: all 0.3s ease;
         &.active {
            top: 0;
            transition: all 0.3s ease;
         }
      }
      @media (max-width: 768px) and (max-height: 600px){
         height: 100%;
         overflow-y: auto;
         &::-webkit-scrollbar{
            display: none;
         }
      }
      @media (max-width: 400px) {
         &.active {
            padding: 34px 15px 23px;
         }
      }
   }
   &__accordion-item {
      width: 135px;
      height: 29px;
      position: relative;
      display: inline-block;
      @media (max-width: 768px) {
         width: 100%;
         max-width: 100%;
         height: auto;
         border-bottom: 2px solid #fff;
         margin: 0 auto;
         padding: 0 30px;
      }
      @media (max-width: 400px) {
         padding: 0 10px;
      }
      &:nth-child(2),
      &:nth-child(3) {
         width: 255px;
         @media (max-width: 768px) {
            width: 100%;
            padding-bottom: 25px;
         }
         @media (max-width: 400px) {
            padding-bottom: 15px;
         }
         .pokedex__accordion-content {
            padding: 13px;
            position: absolute;
            top: 32px;
            @media (max-width: 768px) {
               position: relative;
               top: 0;
               background: #fdfdfd;
               border-radius: 8px;
            }
         }
      }
      &.show {
         .pokedex__accordion-content {
            opacity: 1;
            visibility: visible;
            transition: all 0.3s ease;
            transform: translate(0, 0);
         }
         .pokedex__accordion-header img {
            transform: rotate(180deg);
            transition: all 0.3s ease;
         }
      }
   }
   &__accordion-header {
      display: flex;
      align-items: center;
      justify-content: center;
      background: #f2f2f2;
      box-shadow: 2px 2px 2px rgba(33, 33, 33, 0.1);
      border-radius: 4px;
      padding: 5px 52px;
      position: relative;
      cursor: pointer;
      @media (max-width: 768px) {
         justify-content: flex-start;
         padding: 0;
         background: none;
         cursor: default;
         box-shadow: none;
         img {
            display: none;
         }
      }
      img {
         position: absolute;
         right: 8px;
         transition: all 0.3s ease;
      }
   }
   &__accordion-title {
      text-align: center;
      @media (max-width: 768px) {
         text-align: left;
         font-size: 23px;
         margin-bottom: 20px;
      }
   }
   &__accordion-content {
      background: #f2f2f2;
      box-shadow: 4px 4px 8px rgba(1, 28, 64, 0.2);
      border-radius: 8px;
      padding: 13px 33px;
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease;
      transform: translate(0, -20px);
      z-index: 99;
      position: absolute;
      top: 32px;
      @media (max-width: 768px) {
         padding: 0 0 25px;
         position: relative;
         top: 0;
         background: none;
         opacity: 1;
         transform: translate(0, 0);
         visibility: visible;
         box-shadow: none;
      }
   }
   &__accordion-checkbox {
      position: absolute;
      z-index: -1;
      opacity: 0;
      & + label {
         display: inline-flex;
         align-items: center;
         user-select: none;
      }
      & + label::before {
         content: "";
         display: inline-block;
         width: 12px;
         height: 12px;
         flex-shrink: 0;
         flex-grow: 0;
         border: 1px solid #212121;
         margin-right: 0.5em;
         background-repeat: no-repeat;
         background-position: center center;
         background-size: 50% 50%;
      }
      &:checked + label::before {
         background-color: #212121;
         background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
      }
   }
   &__accordion-text {
      font-family: "Roboto";
      font-weight: 400;
      font-size: 12px;
      color: #000000;
   }
   &__accordion-form {
      display: flex;
      justify-content: space-between;
      gap: 7px;
      flex-wrap: wrap;
      position: relative;
      margin-bottom: 7px;
      &::after {
         content: "";
         position: absolute;
         left: 50%;
         top: 40%;
         transform: translate(-50%, -50%);
         width: 15px;
         height: 1px;
         background-color: #000000;
      }
      .pokedex__accordion-block {
         display: flex;
         flex-direction: column;
         gap: 2px;
         flex: 0 1 42%;
      }
   }
   &__accordion-input {
      border: 1px solid #c4c4c4;
      padding: 4px 10px;
      border: none;
      &::placeholder {
         color: #000000;
      }
      &::-moz-placeholder {
         color: #000000;
      }
      &::-webkit-inner-spin-button {
         display: none;
      }
   }
   &__accordion-btn {
      display: flex;
      justify-content: flex-end;
      flex: 0 1 100%;
      .pokedex__accordion-label {
         padding: 3px 20px;
         background: #73d677;
         border-radius: 11px;
         border: none;
         cursor: pointer;
      }
   }
   &__swiper {
      width: 100%;
      .swiper-slide {
         overflow: hidden;
         display: grid;
         grid-template-columns: repeat(3, 1fr);
         grid-column-gap: 34px;
         grid-row-gap: 45px;
         @media (max-width: 992px) {
            grid-template-columns: repeat(2, 1fr);
         }
         @media (max-width: 768px) {
            grid-template-columns: 1fr;
            gap: 24px;
         }
      }
      .slide {
         display: flex;
         align-items: center;
         height: 100%;
         gap: 5px;
         background: #f6f7f9;
         border-radius: 8px;
         @media (max-width: 768px) {
           height: 200px;
         }
         @media (max-width: 400px){
            height: auto;
         }
         &:nth-child(2) {
            .slide__img {
               background: linear-gradient(270deg, #b33327 0.15%, #d93e30 100%);
            }
         }
         &:nth-child(3),
         &:nth-child(6) {
            .slide__img {
               background: linear-gradient(270deg, #f2cb07 0.15%, #f2b807 100%);
            }
         }
         &:nth-child(4),
         &:nth-child(9) {
            .slide__img {
               background: linear-gradient(270deg, #64d368 0.15%, #64d368 70.88%);
            }
         }
         &:nth-child(8) {
            .slide__img {
               background: linear-gradient(270deg, #f89eae 0.15%, #f4b5c1 100%);
            }
         }
         &__content {
            padding: 5px 0 35px 25px;
         }
         &__title {
            color: #212121;
            text-shadow: 4px 4px 4px rgba(33, 33, 33, 0.1);
            margin-bottom: 22px;
         }
         &__info {
            display: flex;
            gap: 10px;
            margin-bottom: 8px;
         }
         &__info-number {
            border: 3px solid #212121;
            padding: 8px;
            border-radius: 50%;
            text-align: center;
            font-family: "Karla";
            font-weight: 400;
            font-size: 15px;
            color: #212121;
            margin-bottom: 8px;
         }
         &__info-text {
            color: #4b4b4b;
            text-align: center;
         }
         &__skills {
            display: flex;
            align-items: center;
            gap: 12px;
            z-index: 9;
            position: absolute;
         }
         &__img {
            background: linear-gradient(270deg, #5bc7fa 0.15%, #35baff 100%);
            border-radius: 0px 8px 8px 0px;
            width: 100%;
            min-height: 152px;
            height: 100%;
            padding: 0 20px;
            z-index: 0;
            @media (max-width: 400px) {
               padding: 0;
               img {
                  object-fit: contain;
               }
            }
            @media (max-width: 350px) {
               display: flex;
               align-items: center;
            }
            img {
               width: 100%;
               object-fit: cover;
               height: 100%;
            }
         }
         &__skills-item {
            padding: 1px 7px;
            background: #07d6f2;
            box-shadow: inset 0px -2px 0px rgba(0, 0, 0, 0.18);
            border-radius: 11px;
            font-weight: 600;
            &:first-child {
               background: #73d677;
            }
         }
      }
   }
   .swiper-pagination {
      position: relative;
      bottom: 0;
      margin-top: 50px;
      &-bullet {
         background-color: #212121 !important;
         width: 8px;
         height: 8px;
         opacity: 1 !important;
         &-active {
            transform: scale(1.2);
         }
      }
   }
}
.modal__wrapper {
   position: fixed;
   left: 0;
   top: 0;
   bottom: 0;
   right: 0;
   background: #21212180;
   display: flex;
   justify-content: center;
   align-items: center;
   z-index: 1001;
   display: none;
   @media (max-width: 768px) {
      overflow-y: scroll;
      padding: 20px;
      align-items: flex-start;
      &::-webkit-scrollbar {
         display: none;
      }
   }
   @media (max-width: 480px) {
      padding: 10px;
   }
   &#myModal2 {
      .modal__img,
      .modal {
         background: linear-gradient(270deg, #b33327 0.15%, #d93e30 100%);
      }
   }
   &#myModal3,
   &#myModal6 {
      .modal__img,
      .modal {
         background: linear-gradient(270deg, #f2cb07 0.15%, #f2b807 100%);
      }
   }
   &#myModal4,
   &#myModal9 {
      .modal__img,
      .modal {
         background: linear-gradient(270deg, #64d368 0.15%, #64d368 70.88%);
      }
   }
   &#myModal8 {
      .modal__img,
      .modal {
         background: linear-gradient(270deg, #f89eae 0.15%, #f4b5c1 100%);
      }
   }
   .modal {
      position: relative;
      max-width: 800px;
      width: 100%;
      background: linear-gradient(270deg, #5bc7fa 0.15%, #35baff 100%);
      border-radius: 16px;
      display: flex;
      gap: 12px;
      @media (max-width: 768px) {
         flex-direction: column;
      }
      &__close {
         position: absolute;
         right: 0px;
         top: -50px;
         cursor: pointer;
         @media (max-width: 768px) {
            right: 10px;
            top: 10px;
            z-index: 10101;
         }
         @media (max-width: 350px) {
            width: 23px;
            img {
               width: 100%;
               height: auto;
            }
         }
      }
      &__img {
         background: linear-gradient(270deg, #5bc7fa 0.15%, #35baff 100%);
         box-shadow: 4px 4px 8px rgba(1, 28, 64, 0.2);
         border-radius: 8px 0px 0px 8px;
         max-width: 360px;
         min-height: 400px;
         width: 100%;
         height: 100%;
         position: relative;
         display: flex;
         align-items: center;
         justify-content: center;
         @media (max-width: 768px) {
            min-height: fit-content;
            max-width: 100%;
            border-radius: 8px;
            padding: 20px 0;
            picture {
               display: flex;
               align-items: center;
               justify-content: center;
            }
            img {
               width: 70%;
            }
         }
         picture {
            width: 100%;
            height: 100%;
         }
         img {
            width: 100%;
            height: 100%;
            object-fit: cover;
         }
      }
      &__content {
         padding: 30px 20px 30px 0;
         width: 100%;
         @media (max-width: 768px) {
            padding: 30px 10px;
         }
         @media (max-width: 480px) {
            padding: 20px 10px;
         }
      }
      &__img-block {
         display: flex;
         align-items: center;
         gap: 10px;
         position: absolute;
         right: 20px;
         bottom: 20px;
      }
      &__img-item {
         padding: 1px 7px;
         background: #07d6f2;
         box-shadow: inset 0px -2px 0px rgba(0, 0, 0, 0.18);
         border-radius: 11px;
         font-weight: 600;
         font-family: "Roboto";
         font-weight: 400;
         font-size: 12px;
         text-align: center;
         color: #212121;
         &:first-child {
            background: #73d677;
         }
      }
      &__header {
         display: flex;
         align-items: flex-end;
         justify-content: space-between;
         margin-bottom: 25px;
         &-item {
            display: flex;
            align-items: flex-end;
            gap: 8px;
            @media (max-width: 480px) {
               align-items: center;
            }
         }
         @media (max-width: 480px) {
            flex-direction: column;
            align-items: center;
            gap: 5px;
         }
      }
      &__title {
         color: #fdfdfd;
         text-shadow: 4px 4px 4px rgba(33, 33, 33, 0.1);
      }
      &__subtitle {
         font-family: "Source Sans Pro";
         font-weight: 400;
         font-size: 24px;
         color: #fdfdfd;
      }
      &__header-number {
         width: 41px;
         height: 41px;
         background: #f2cb07;
         border-radius: 50%;
         display: flex;
         justify-content: center;
         align-items: center;
         font-family: "Karla";
         font-weight: 400;
         font-size: 16px;
         color: #000000;
      }
      &__ability {
         padding: 13px 28px;
         background: #fdfdfd;
         box-shadow: 4px 4px 4px rgba(33, 33, 33, 0.1);
         border-radius: 8px;
         max-width: 264px;
         margin-bottom: 21px;
         @media (max-width: 768px) {
            max-width: 100%;
         }
      }
      &__points {
         background: #fdfdfd;
         box-shadow: 4px 4px 4px rgba(33, 33, 33, 0.1);
         border-radius: 8px;
         padding: 20px;
         display: flex;
         justify-content: space-between;
         align-items: center;
         margin-bottom: 21px;
         @media (max-width: 350px) {
            flex-direction: column;
            gap: 10px;
            padding: 10px;
         }
         &-item {
            flex: 0 1 45%;
            width: 100%;
            @media (max-width: 350px) {
               flex: 1 1 100%;
            }
            &:nth-child(2) {
               .modal__progress {
                  background: linear-gradient(180deg, #f5db13 0%, #f2b807 100%);
               }
            }
         }
      }
      &__progressbar {
         background: #f6f7f9;
         border-radius: 8px;
         width: 100%;
         height: 5px;
      }
      &__progress {
         width: 80%;
         height: 5px;
         background: linear-gradient(270deg, #64d368 0.15%, #64d368 70.88%);
         border-radius: 8px;
      }
      &__info {
         display: flex;
         align-items: center;
         justify-content: space-between;
         @media (max-width: 768px) {
            gap: 5px;
            flex-wrap: wrap;
            justify-content: center;
         }
         &-item {
            background: #fdfdfd;
            box-shadow: 4px 4px 24px rgba(1, 17, 38, 0.2);
            border-radius: 8px;
            text-align: center;
            padding: 8px 20px;
            @media (max-width: 768px) {
               padding: 8px 15px;
            }
         }
         &-number {
            width: 36px;
            height: 36px;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 3px solid #212121;
            border-radius: 50%;
            margin: 0 auto 6px;
         }
         &-text {
            font-family: "Karla";
            font-weight: 400;
            font-size: 12px;
            color: #4b4b4b;
         }
      }
   }
}
