/* Default */
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
:focus-visible {
	outline: none;
}
body {
	overflow-x: hidden !important;
	line-height: 1.17;
	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	height: 100%;
	position: relative;
	&::after{
		content: '';
		position: fixed;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		background: #F5DB13;
		z-index: 9999;
		transition: all .3s ease;
	}
	&.loaded::after{
		opacity: 0;
		visibility: hidden;
		transition: all .3s ease;
	}
}
.body__home {
	background: #F5DB13;
}
.body__legend {
	background: #1D1E1D;
	.footer__text {
		color: #fff;
	}
}
.body__pokedex {
	background: #fff;
}
.main {
	flex: 1 1 auto;
}
.container {
	max-width: $mw;
	margin: 0 auto;
	width: 100%;
	@media (max-width: $md1) {
		max-width: 970px;
	}
	@media (max-width: $md2) {
		max-width: 750px;
	}
	@media (max-width: $md3) {
		max-width: none;
		padding: 0 10px;
	}
}
a {
	text-decoration: none;
}
h1,
h2,
h3,
h4,
p,
span {
	margin: 0;
	padding: 0;
}
ul,
li {
	padding: 0;
	margin: 0;
	list-style: none;
}
/* Main */
.title {
	font-family: "Karla";
	font-weight: 400;
	font-size: 72px;
	letter-spacing: 4px;
	@media (max-width: 600px){
		font-size: 54px;
	}
	@media (max-width: 480px) {
		font-size: 42px;
	}
}
.subtitle {
	font-family: "Karla";
	font-weight: 400;
	font-size: 32px;
	@media (max-width: 480px) {
		font-size: 24px;
	}
}
.subtitle-big {
	font-family: "Karla";
	font-weight: 700;
	font-size: 18px;
}
.subtitle-small {
	font-family: "Karla";
	font-weight: 400;
	font-size: 12px;
}
.text {
	font-family: "Source Sans Pro";
	font-weight: 400;
	font-size: 16px;
}
.subtext {
	font-family: "Karla";
	font-weight: 700;
	font-size: 36px;
}
.subtext-big {
	font-family: "Karla";
	font-weight: 400;
	font-size: 16px;
}
.subtext-small {
	font-family: "Karla";
	font-weight: 400;
	font-size: 36px;
	line-height: 101.5%;
	letter-spacing: 4px;
}
