.section {
   &__rocket {
      background: #d93e30;
   }
}
.roket {
   padding: 100px 0;
   min-height: 100vh;
   height: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   height: 100%;
   &__img {
      position: relative;
      z-index: 1;
      &::after{
         content: '404';
         position: absolute;
         left: 50%;
         top: 50%;
         transform: translate(-50%, -50%);
         font-family: 'Karla';
         font-weight: 700;
         font-size: 700px;
         letter-spacing: 4px;
         color: rgba(0, 0, 0, 0.5);
         z-index: -1 !important;
         @media (max-width: 1137px){
            font-size: 450px;
         }
         @media (max-width: 768px){
            font-size: calc(150px + (450 - 150) * (100vw - 320px) / (768 - 320));
         }
      }
      img{
         z-index: 1 !important;
         width: 100%;
      }
   }
   &__title {
      font-family: 'Karla';
      font-weight: 700;
      font-size: 48px;
      color: #F2F2F2;
      margin-bottom: 62px;
      text-align: center;
      @media (max-width: 480px){
         font-size: 25px;
      }
      span{
         color: #000000;
      }
   }
   &__btn {
      font-family: 'Karla';
      font-weight: 700;
      font-size: 23px;
      color: #212121; 
      padding: 17px 79px;
      background: #F2CB07;
      box-shadow: inset 0px -9px 0px rgba(0, 0, 0, 0.18);
      border-radius: 11px;
      transition: all .3s ease;
      &:hover{
         background: #ddb905;
         box-shadow: inset 0px 0px 0px rgba(0, 0, 0, 0.18);
         transition: all .3s ease;
      }
   }
}
