footer{
   padding: 33px 0;
   @media (max-width: 768px){ 
      padding: 20px 0;
   }
}
.footer {
   display: flex;
   justify-content: space-between;
   align-items: center;
   @media (max-width: 768px){
      flex-direction: column;
      gap: 10px;
      justify-content: center;
      text-align: center;
   }
}