.section {
   &__legend {
      padding: 40px 0 100px;
   }
}
.legend {
   display: flex;
   flex-direction: column;
   gap: 64px;
   &__item {
      position: relative;
   }
   &__title {
      color: #ffffff;
      padding-bottom: 19px;
      border-bottom: 1px solid #ffffff;
      margin-bottom: 45px;
      @media (max-width: 480px){
         font-size: 44px;
      }
   }
   .swiper-big {
      .slide {
         display: flex;
         justify-content: space-between;
         align-items: center;
         gap: 275px;
         padding: 0 0 50px;
         @media (max-width: 1137px) {
            gap: 100px;
         }
         @media (max-width: 992px) {
            gap: 20px;
         }
         @media (max-width: 768px){
            flex-direction: column;
            align-items: flex-start;
         }
         &__img {
            width: 100%;
            height: 100%;
            img {
               width: 100%;
               height: 100%;
               object-fit: contain;
            }
         }
         &__content {
            max-width: 406px;
            @media (max-width: 768px){
              max-width: 100%;
            }
         }
         &__title {
            color: #ffffff;
         }
         &__text {
            font-weight: 400;
            color: #ffffff;
            margin-bottom: 30px;
            @media (max-width: 480px){
               font-size: 14px;
            }
         }
         &__info {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
            column-gap: 40px;
            row-gap: 21px;
            @media (max-width: 768px){
               justify-content: flex-start;
            }
            @media (max-width: 480px){
               gap: 20px;
            }
         }
         &__info-item {
            width: 175px;
            @media (max-width: 480px){
               width: 46%;
            }
         }
         &__info-text {
            color: #ffffff;
         }
         &__info-progressbar {
            border-radius: 8px;
            background: #F6F7F9;
            width: 100%;
            height: 7px;
            margin-top: 2px;
         }
         &__info-progress {
            width: 80%;
            height: 100%;
            border-radius: 8px;
            background-color: #F5DB13;
         }
      }
   }
   .swiper-small {
      z-index: 1;
      position: relative;
      margin: 0 50px;
      .swiper-slide {
         opacity: 0.5;
         &-thumb-active{
            opacity: 1;
         }
      }
      .slide {
         background: linear-gradient(269.46deg, #B57E10 -7.62%, #B57E10 13.9%, #F9DF7B 28.5%, #FFF3A6 45.42%, #F9DF7B 62.17%, #B57E10 81.71%);
         border-radius: 16px;
         &__img {
            img {
               width: 100%;
               height: 100%;
            }
         }
         &__content {
            background: linear-gradient(180deg, #FFFFFF 30.32%, #F5F5F5 100%);
            border-radius: 16px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 15px 8px;
         }
         &__subtitle {
            font-family: "Roboto";
            font-weight: 400;
            font-size: 18px;
            color: #1d1e1d;
         }
      }
   }
   .swiper__btns {
      height: 25px;
      position: absolute;
      left: 0px;
      right: 0px;
      bottom: calc(25px + 181px/2);
      transform: translate(0, 100%);
      z-index: 0;
      display: flex;
      align-items: center;
      @media (max-width: 400px){
         bottom: calc(25px + 250px/2);
      }
      @media (max-width: 350px){
         bottom: calc(25px + 220px/2);
      }
      @media (max-width: 320px){
         bottom: calc(25px + 200px/2);
      }
   }
   .swiper-button-next,
   .swiper-button-prev {
      top: 0 !important;
      margin-top: 0 !important;
      &::after {
         font-family: swiper-icons;
         font-size: 50px;
         text-transform: none !important;
         letter-spacing: 0;
         text-transform: none;
         font-variant: initial;
         line-height: 2;
         color: #f6f7f9;
      }
   }
}
