header {
   nav {
      background: #f5db13;
      box-shadow: 4px 4px 16px rgba(1, 28, 64, 0.2);
      &.fixed__nav {
         position: fixed;
         left: 0;
         top: 0;
         right: 0;
         z-index: 999;
         .nav {
            padding: 10px 0;
         }
      }
      .nav {
         display: flex;
         justify-content: space-between;
         align-items: center;
         padding: 16px 0;
         position: relative;
      }
      .logo {
         @media (max-width: 768px) {
            img {
               width: 121px;
            }
         }
         @media (max-width: 480px) {
            img {
               width: 58px;
            }
         }
      }
      .menu {
         @media (max-width: 768px) {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 400px;
            position: fixed;
            left: 0;
            right: 0;
            z-index: 999;
            background: linear-gradient(180deg, #f5db13 0%, #f2b807 100%);
            box-shadow: 4px 4px 24px rgba(1, 17, 38, 0.2);
            border-radius: 0px 0px 16px 16px;
            top: -100%;
            transition: all 0.3s ease;
            &.active {
               top: 0;
               transition: all 0.3s ease;
            }
         }
      }
      @media (max-width: 768px) and (max-height: 600px) {
         height: 100%;
         overflow-y: auto;
         &::-webkit-scrollbar {
            display: none;
         }
      }
      .list {
         display: flex;
         align-items: center;
         gap: 66px;
         @media (max-width: 992px) {
            gap: 30px;
         }
         @media (max-width: 768px) {
            margin-top: 40px;
            flex-direction: column;
            justify-content: center;
            text-align: center;
            &__link {
               font-size: 32px;
            }
         }
         li {
            position: relative;
            &::after {
               content: "";
               position: absolute;
               bottom: -10px;
               right: 0;
               left: 0;
               height: 2px;
               background-color: #212121;
               transform: scaleX(0) translate(0, -5px);
               transition: all 0.3s ease;
            }
            &:hover::after {
               transform: scaleX(1) translate(0, 0);
               transition: all 0.3s ease;
            }
         }
         &__link {
            font-family: "Karla";
            font-weight: 400;
            font-size: 25px;
            color: #212121;
         }
      }
      @media (max-width: 768px) {
         .logo {
            z-index: 1000;
            &.active {
               display: flex;
               width: 100%;
               justify-content: center;
            }
         }
         .burger {
            width: 26px;
            height: 21px;
            position: relative;
            z-index: 1000;
            &::after,
            &::before,
            span {
               content: "";
               position: absolute;
               width: 100%;
               height: 4px;
               left: 0;
               right: 0;
               background-color: #1d1e1d;
               transition: 0.3s ease 0s;
               transform-origin: right;
               border-radius: 5px;
            }
            &::before {
               top: 0px;
            }
            &::after {
               bottom: 0px;
            }
            span {
               top: 50%;
               transform: translate(0, -50%);
            }
            &.active {
               &::before {
                  transform: rotate(-45deg);
                  transition: 0.3s ease 0s;
               }
               &::after {
                  transform: rotate(45deg);
                  transition: 0.3s ease 0s;
               }
               span {
                  transform: translate(0, -50%) scaleX(0);
                  transition: 0.3s ease 0s;
               }
            }
         }
      }
   }
   .section {
      &__start {
         padding: 128px 0 200px;
         @media (max-width: 992px) {
            padding: 128px 0;
         }
         @media (max-width: 768px) {
            padding: 18px 0 41px;
         }
      }
   }
   .start {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media (min-width: 1920px) {
         position: relative;
      }
      @media (max-width: 992px) {
         flex-direction: column-reverse;
         justify-content: center;
      }
      &__item {
         &.first-item {
            max-width: 516px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: 64px;
            z-index: 5;
            @media (max-width: 992px) {
               max-width: 100%;
               align-items: center;
               text-align: center;
               gap: 30px;
            }
         }
         &.second-item {
            position: absolute;
            right: -50px;
            @media (max-width: 1137px) {
               max-width: 600px;
            }
            @media (max-width: 992px) {
               position: relative;
               right: 0;
            }
            img {
               width: 100%;
               height: auto;
            }
         }
      }
      &__btn {
         font-family: "Karla";
         font-style: normal;
         font-weight: 700;
         font-size: 23px;
         line-height: 27px;
         color: #212121;
         padding: 17px 36px;
         background: #73d677;
         box-shadow: inset 0px -9px 0px rgba(0, 0, 0, 0.18);
         border-radius: 11px;
         transition: all 0.3s ease;
         &:hover {
            background: #65bb68;
            transition: all 0.3s ease;
            box-shadow: inset 0px 0px 0px rgba(0, 0, 0, 0.18);
         }
      }
   }
}
